/* logo */
.gradient-text-flag {
  background: linear-gradient(90deg, #e37f0c, #ffffff, #0c7e01);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}


/* text slider section for big screen */
.textSlider{

  animation: slideRight 30s linear infinite;
  animation-delay: 8s;
}
.textSliderfast{

  animation: slideRight 30s linear infinite;
  animation-delay: 4s;
}
.textSliderfaster{

  animation: slideRight 30s linear infinite;
}

/* Text sider section for small screen */
@media (max-width:476px){
  .textSlider{

    animation: slideRightLeft 18s linear infinite;
    
  }
  .textSliderfast{
  
    animation: slideRightLeft 18s linear infinite;
    
  }
  .textSliderfaster{
  
    animation: slideRightLeft 18s linear infinite;
  }
}
/* Slider animation for large screen */

@keyframes slideRight {
  0% {
    transform: translateX(0%);
  }
  50%{
    transform: translate(52%);
  }
  100% {
    transform: translateX(0%);
  }
}
/* Sldier animation for small screen */
@keyframes slideRightLeft {
  0% {
    transform: translateX(-80%);
  }
  50% {
    transform: translateX(50%);
  }
  100% {
    transform: translateX(-80%);
  }
}

/* Easy navigion section  */
@media (max-width:476px) {
  .easy-navigation-button-small-screen{
    width: 42vw;
  }
  .display-block-small-screen-explore{
    display: block;
    width: 97vw;
    padding: 5px;
  }
}


